import React from 'react';
import './Login.css';

const Login = () => {
    return(
        <>
            <div className="logincontainer">
                <div className="loginmain">
                <div className="formdata">
                    <form className="my_form">
                    <h2>Login </h2>
                        <div className="label1">
                        Email:<input type="text" name="name" placeholder="Email" autoComplete='off' className="text1"/> <br/>
                        Password:<input type="text" name="name" placeholder='Password' autoComplete='off' className="text2"/>
                        </div>
                        <div className="login_label">
                        <div className="input1">
                        <input type='submit' name="submit" value="Login" placeholder='Email'/>
                        </div>
                        <div className="input2">
                        <input type='submit' name="submit" value="Forgot Password ?" placeholder='Forgot'/>
                        </div>
                        </div>
                    </form>

                </div>

                </div>

            </div>
        </>
    )
}

export default Login;