import React from 'react';
import './Layout.css';
const Layout = () =>{
    return(
        <>
            <div className="container">
                <div className="main">
                    <div className="top_block">
                    <form>
                        <div className="search">
                        <input type="text" name="name" placeholder="search"/>
                            <select className="select_option">
                                <option value="option1" className="option">Choose Category</option>
                                <option value="option2"  className="option"><li>House</li></option>
                                <option value="option3"  className="option"><li>Room</li></option>

                            </select>
                            <input type="submit" name="submit" value="search"/>

                         </div>
                         </form>
                    </div>
                
                    <div className="center_block">
                        <div className="cards">
                        <h1>Houses</h1>

                        <div className="card">
                            <div className="card1"></div>
                            <div className="card1"></div>
                            <div className="card1"></div>
                            <div className="card1"></div>
                            <div className="card1"></div>
                         </div>
                        <div className="card">
                            <div className="card1"></div>
                            <div className="card1"></div>
                            <div className="card1"></div>
                            <div className="card1"></div>
                            <div className="card1"></div>
                        </div>
                        <div className="card">
                            <div className="card1"></div>
                            <div className="card1"></div>
                            <div className="card1"></div>
                            <div className="card1"></div>
                            <div className="card1"></div>
                        </div>
                    

                        </div>

                    </div>
                    <div className="mid_block">
                        <div className="featured">
                            <h2>Featured</h2>
                            <div className="featured_post">
                                <div className="post1"></div>
                                <div className="post1"></div>
                                <div className="post1"></div>
                                <div className="post1"></div>
                                <div className="post1"></div>



                            </div>

                        </div>
                    </div>
                    <div className="bottom"></div>
                </div>
            </div>
        </>
    )
}


export default Layout;