import React from 'react';
import Header from './Header';
import {BrowserRouter,Switch,Route}from 'react-router-dom';

import Home from './UserPages/Home';
import House from './UserPages/House';
import Room from './UserPages/Room';
import About from './UserPages/About';
import Contact from './UserPages/Contact';
import Login from './UserPages/UserLogin/Login';
import Register from './UserPages/UserRegister/Register';
// import Footer from './Footer';
// import HousePage from '../UserUI/UserLayout/HouseLayout/HousePage'
// import HouseDetails from '../UserUI/UserPages/HouseDetails';
 
const App = () =>{
    return(
        <>
            <div>
            
            <BrowserRouter>
            <Header/>

                <Switch>
                    <Route path="/" exact component={Home}/>
                    <Route path="/house" component={House}/>
                    <Route path="/room" component={Room}/>
                    <Route path="/about" component={About} />
                    <Route path="/contact" component={Contact}/>
                    <Route path="/login" component={Login}/>
                    <Route path="/register" component={Register}/>




                </Switch>
=           


                {/* <Footer/> */}

            </BrowserRouter>

            </div>
        </>
    )
}

export default App;
  
  