import React from 'react';
import './Register.css';

const Register = () => {
    return(
        <>
            <div className="register_container">
                <div className="register_main">
                <div className="form_data">
                    <form className="myform">
                    <h2>Register</h2>
                        <div className="label1">
                        Name:<input type="text" name="name" placeholder="Email" autoComplete='off' className="text1"/> <br/>
                        Mobile:<input type="text" name="name" placeholder='Mobile' autoComplete='off' className="text1"/>
                        Email:<input type="text" name="name" placeholder="Email" autoComplete='off' className="text1"/> <br/>
                        Address:<input type="text" name="name" placeholder="Address" autoComplete='off' className="text1"/> <br/>
                        Password:<input type="text" name="name" placeholder='Password' autoComplete='off' className="text1"/>
                        </div>
                        <div className="login_label">
                        <div className="input1">
                        <input type='submit' name="submit" value="Login" placeholder='Email'/>
                        </div>
                        <div className="input2">
                        <input type='submit' name="submit" value="Sign Up" placeholder='Forgot'/>
                        </div>
                        </div>
                    </form>

                </div>

                </div>

            </div>
        </>
    )
}

export default Register;