import React from 'react';
import './HousePage.css';
import Sidebar from './Sidebar';
import {Link} from 'react-router-dom';


import image from '../../../../Assets/Images/house.jpeg';
const AboutPage = () => {
  return (
    <>
      <div className="house_container">
        <div className="house_main">

          <div className="home_cards">

            <div className="card">
              <div className="image">
                <img src={image} alt="img" />
              </div>
              <div className="cardheader">
                <h2>House Name : Shresth House</h2>
              </div>
              <div className="cardbody">
              <p></p>
              <p>Address : Chabahil, kathmandu</p>
              <p>House No</p>
              <p>House Owner : Bishal Shrestha</p>
              <p>Price : Rs 3,00,000</p>

              <p>Features</p>
              <ul>
                  <li>Total Area 1 Ropani</li>
                  <li>House has 6 room with 2 bathroom under 2 tala</li>
                  <li>Garden area</li>
                  <li>Water alailable</li>
                  <li>Parking Space for bike and car</li>
                  <li>Suitable for Business and Office</li>
              </ul>
              <button><Link to="#">House Details</Link> </button>
              </div>

            </div>

           


          </div>
        </div>

      </div>
      <Sidebar />
    </>

  )
}

export default AboutPage;